import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import Header from '../_layout/header';
import Footer from '../_layout/footer';
import { APP_INFO, UpdateMenuOptions } from '../../config/app-settings';
import { FetchUserData, GetUserData } from '../../api/user.api';
import { UserProvider } from '../../config/user-context';
import Routes from './routes';
import LoadingUserInfo from '../_layout/loading-user-info';

class App extends React.Component {
    constructor(props) {
        super(props);

        const user = GetUserData();

        this.state = {
            _userDidLoad: true,
            user: user,
            menuOptions: UpdateMenuOptions(user.roles),
            date: null
        };
    }

    GetUser = () => {
        FetchUserData().then(user => {
            const newOptions = UpdateMenuOptions(user.roles);
            this.setState({ user: user, menuOptions: newOptions, _userDidLoad: true });
        });
    };

    componentDidMount() {
        //this.GetUser();
    }

    render() {
        const { user, menuOptions, _userDidLoad } = this.state;

        return (
            <Router>
                <UserProvider user={user}>
                    <Header
                        logoPrincipal={APP_INFO.logo}
                        nombreLogo={APP_INFO.nombreDesc}
                        userFullName={user.fullname}
                        menuOptions={menuOptions}
                    />

                    <div className="container contenido-principal">
                        <Routes roles={user.roles} />
                        {/* {!_userDidLoad && <LoadingUserInfo />} */}
                        {/* {_userDidLoad && <Routes roles={user.roles} />} */}
                    </div>

                    <Footer nombreSistema={APP_INFO.nombreFooter} />
                    <section id="modal-section" />
                </UserProvider>
            </Router>
        );
    }
}

export default App;
